
.user-register .swiper-slide {
	padding: 0 1.3rem;
	box-sizing: border-box;
}
.user-register .hd {
	padding: 0 3rem;
	margin-bottom: .6rem;
	font-size: 1.05rem;
	text-align: center;
	border-bottom: 1px solid #F3F4F9;
}
.user-register .hd li {
	padding: .6rem 0;
	height: 2.6rem;
	line-height: 2.6rem;
	display: block;
	margin-bottom: -1px;
	position: relative;
}
.user-register .bd {
	padding-bottom: 1rem !important;
}
.lo-tip {
	color: #7D7B7B;
	font-size: 13px;
	margin-top: .6rem;
	float: right;
}
.ipt-check-btn {
	padding: 0 1.4rem;
	height: 1.6rem;
	line-height: 1.6rem;
	margin: .7rem 0;
	text-align: center;
	color: #555;
	display: block;
	border-left: 1px solid #F3F4F9;
	margin-left: 1.2rem;
	font-size: 1rem;
}
.text-all .input-text span {
	position: absolute;
	right: 22px;
	top: 8px;
}



