
.cart {
	width: 30px;
	height: 30px;
	position: fixed;
	z-index: 10000;
	bottom: -60px;
	left: 50%;
	margin-left: -15px;
	border: 4px solid #ff4146;
	border-radius: 50%;
	background: #fff;
	padding: 5px;
	box-shadow: 0 0 2px 5px #ffb6c1;
}
.cart img {
	width: 100%;
}
.move {
	animation: mymove 1.5s;
}
@keyframes mymove {
0 {
		bottom: -60px;
}
20% {
		bottom: 65px;
}
80% {
		bottom: 65px;
}
100% {
		bottom: -60px;
}
}



